import PressPopup from '../press-popup-plus/press-popup-plus.vue';
import Options from './options.vue';
import { defaultProps, defaultOptions } from '../common/component-handler/press-component';
import computed from './computed';

export default {
  name: 'PressShareSheet',
  options: {
    ...defaultOptions,
  },
  components: {
    PressPopup,
    Options,
  },
  props: {
    show: Boolean,
    overlayStyle: { type: String, default: '' },
    zIndex: {
      type: Number,
      default: 100,
    },
    title: { type: String, default: '' },
    cancelText: {
      type: String,
      default: '取消',
    },
    description: { type: String, default: '' },
    options: {
      type: Array,
      default: () => [],
    },
    overlay: {
      type: Boolean,
      default: true,
    },
    safeAreaInsetBottom: {
      type: Boolean,
      default: true,
    },
    closeOnClickOverlay: {
      type: Boolean,
      default: true,
    },
    duration: {
      type: Number,
      default: 300,
    },
    ...defaultProps,
  },
  emits: ['click-overlay', 'cancel', 'select', 'close'],
  data() {
    return {
      computed,
    };
  },
  methods: {
    onClickOverlay() {
      this.$emit('click-overlay');
    },
    onCancel() {
      this.onClose();
      this.$emit('cancel');
    },
    onSelect(event) {
      this.$emit('select', event);
    },
    onClose() {
      this.$emit('close');
    },
  },
};

